#filter-controls {
  display: flex;
  align-items: center;
  position: relative;
  width: 120px;
  flex: 3;
}
#filter {
  background-color: rgb(182, 247, 122);
  border-radius: 20px;
  height: 12px;
  width: 12px;
  padding: 3px;
  position: relative;
  top: -1px;
  border: solid 1px rgb(0, 0, 0, .7);
  z-index: 10;
}
#filter-menu {
  width: 80px;
  height: 14px;
  font-size: 10px;
  border: none;
  background-color: rgb(182, 247, 122);
  position: absolute;
  left: 16px;
  z-index: 1;
  text-indent: 2px;
}
#sorting-button {
  width: 12px;
  height: 12px;
  border: none;
  background-color: #015f3d;
  position: relative;
  left: 78px;
  top: -3px;
}
#sorting-icons {
  width: 16px;
  cursor: pointer;
}
.filter-tag {
  position: absolute;
  background-color: rgb(71, 71, 71);
  color: white;
  padding: 6px;
  border-radius: 4px;
  font-family: arial;
  font-size: 10px;
  top: 14px;
  right: 36px;
  display: none;
}
.filter-btn-tag {
  position: absolute;
  background-color: rgb(71, 71, 71);
  color: white;
  padding: 6px;
  border-radius: 4px;
  font-family: arial;
  font-size: 10px;
  top: 14px;
  right: 5px;
  display: none;
}
#filter-menu:hover + .filter-tag {
  display: block;
}
#sorting-button:hover + .filter-btn-tag {
  display: block;
}
