#movie-form {
  display: flex;
  justify-content: center;
}
.form-display-btn-container {
  position: fixed;
  background-color: #015f3d;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
  top: 192px;
  box-shadow: 0px 2px 1px rgb(0, 0, 0);
  transition: display .5s;
}
#form-display-btn {
  position: relative;
  color: white;
  background-color: black;
  border: none;
  box-shadow: 2px 2px 0 rgba(255, 255, 255, 1);
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 16px;
  transition: left .2s, top .2s;
  width: 80%;
  z-index: 100;
  height: 26px;
  margin-bottom: 4px;
}
#form-display-btn:hover {
  color: rgb(182, 247, 122);
  box-shadow: 2px 2px 0 rgb(182, 247, 122);
}
#form-display-btn:active {
  color: rgb(255, 255, 255);
  left: 2px;
  top: 2px;
  box-shadow: none;
}
form {
  padding: 6px;
  box-shadow: 0px 2px 1px rgb(0, 0, 0);
  background-color: #015f3d;
  font-family: 'Tektur', cursive;
  font-size: 13px;
  color: rgb(182, 247, 122);
  text-shadow: 1px 1px 2px rgb(0, 0, 0);
  position: fixed;
  width: 100%;
  top: 192px;
  z-index: 99;
  transition: display .5s;
}
input {
  border: solid 1px black;
  background-color: rgb(182, 247, 122);
}
:link {
  text-shadow: 1px 1px 2px rgb(138, 138, 138);
}
#poster-control {
  display: flex;
  justify-content: space-evenly;
}
#image-url {
  margin-left: 4px;
  width: 210px;
}
#image-url::placeholder {
  text-align: center;
}
#new-movie-controls {
  display: flex;
  padding: 6px 0px;
  justify-content: space-evenly;

}
#new-movie-control {
  display: flex;
}
#movie-title {
  width: 100px;
  margin-left: 4px;
}
#rating {
  width: 48px;
  margin-left: 4px;
}
.new-movie-actions {
  display: flex;
  justify-content: space-evenly;
}
.button {
  margin: 0px 6px;
  color: white;
  background-color: black;
  border: none;
  padding: 4px 8px;
  box-shadow: 2px 2px 0 rgba(255, 255, 255, 1);
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 16px;
  position: relative;
  transition: left .2s, top .2s;
  width: 100px;
  margin: 6px;
}
.button:hover {
  color: rgb(182, 247, 122);
  box-shadow: 2px 2px 0 rgb(182, 247, 122);
}
.button:active {
  color: rgb(255, 255, 255);
  left: 2px;
  top: 2px;
  box-shadow: none;
}
.hidden {
  display: none;
}
