#splash-art {
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  width: 100%;
  z-index: 100;
}
#splash-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  padding: none;
}
h1 {
  z-index: 10;
  position: absolute;
  color: rgb(182, 247, 122);
  font-family: 'Permanent Marker', cursive;
  text-shadow: rgb(0, 0, 0) 2px 2px 12px, -2px -2px 12px black;
  font-size: 38px;
}

