#search-form {
  display: flex;
  align-items: center;
  position: relative;
  background: none;
  border: none;
  flex: 6;
  justify-content: center;
}
#search-bar {
  border-radius: 20px;
  background-color: rgb(182, 247, 122);
  color: black;
  font-family: Arial, Helvetica, sans-serif;
  border: solid 1px rgb(0, 0, 0, 1);
  width: 224px;
  text-indent: 4px;
}
#search-bar::placeholder {
  font-family: 'Ubuntu', sans-serif;
  font-size: 12px;
  color: black;
}
#reset {
  height: 12px;
  width: 12px;
  z-index: 10;
  position: absolute;
  right: 53px;
  cursor: pointer;
}
#search {
  height: 14px;
  width: 14px;
  background-color: rgb(182, 247, 122);
  padding: 2px 4px;
  border-left: solid 1px rgb(0, 0, 0);
  z-index: 10;
  position: absolute;
  right: 18px;
  border-top-right-radius: 18px;
  border-bottom-right-radius: 18px;
  cursor: pointer;
}
.search-tag {
  position: absolute;
  background-color: rgb(71, 71, 71);
  color: white;
  padding: 6px;
  border-radius: 4px;
  font-family: arial;
  font-size: 10px;
  right: 6px;
  top: 14px;
  display: none;
}
#search:hover + .search-tag {
  display: block;
}
