.movie-element {
  height: 56vh;
  width: 300px;
  display: flex;
  flex-direction: column;
  background-color: #015f3d;
  margin-top: 18px;
  box-shadow: 0px 0px 10px 2px black;
  position: relative;
  justify-content: center;
}
.poster {
  height: 400px;
  width: 100%;
  object-fit: fill;
  position: relative;
  border-bottom: 1px solid #001a10;
}
textarea {
  position: absolute;
  margin: 12px;
  width: 268px;
  height: 368px;
  font-family: 'Roboto Condensed', sans-serif;
  top: -2px;
  display: block;
  overflow: scroll;
  background-color: rgb(182, 247, 122, 0.5);
  border: none;
  color: black;
  font-size: 22px;
  z-index: 1;
  padding: 4px;
}
.note-hover-display {
  position: absolute;
  margin: 12px;
  width: 272px;
  height: 372px;
  font-family: 'Roboto Condensed', sans-serif;
  top: -2px;
  right: -1px;
  opacity: 0%;
  overflow: scroll;
  background-color: rgba(0, 15, 83, 0.5);
  border: none;
  color: rgb(255, 255, 255);
  font-size: 22px;
  z-index: 10;
  transition: opacity 1s;
  padding: 4px;
  text-shadow: 1px 1px 4px rgb(119, 119, 119), -1px -1px 4px rgb(119, 119, 119);
}
.note-hover-display:hover {
  opacity: 100%;
}
.del-controls {
  position: absolute;
  width: 284px;
  z-index: 11;
  top: 0px;
  right: -3px;
  background-color: #001a10;
  padding: 12px;
  margin: 0;
  color: rgb(182, 247, 122);
  display: flex;
  flex-direction: column;
  box-shadow: 1.5px 2px 4px rgb(182, 247, 122), -2px 2px 4px rgb(182, 247, 122);
  font-family: 'Tektur', cursive;
  font-size: 13px;
}
.btn-row {
  display: flex;
  justify-content: end;
}
.del-controls h3 {
  margin: 0;
  text-shadow: 1px 1px 2px rgb(117, 111, 57);
}
.del-controls button {
  color: white;
  background-color: black;
  border: none;
  padding: 4px 8px;
  box-shadow: 2px 2px 0 rgba(255, 255, 255, 1);
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 16px;
  position: relative;
  transition: left .2s, top .2s;
  width: 60px;
  margin-left: 24px;
}
.del-controls button:hover {
  color: rgb(182, 247, 122);
  box-shadow: 2px 2px 0 rgb(182, 247, 122);
}
.del-controls button:active {
  color: rgb(255, 255, 255);
  left: 2px;
  top: 2px;
  box-shadow: none;
}
.del-controls .danger:hover {
  color: rgb(255, 0, 0);
  box-shadow: 2px 2px 0 rgb(255, 0, 0);
}
.del-controls .danger:active {
  color: rgb(255, 0, 0);
  left: 2px;
  top: 2px;
  box-shadow: none;
}
.title-rating-buttons {
  display: flex;
  position: relative;
  align-items: center;
  padding: none;
  justify-content: space-between;
  height: 100%;
  /* overflow: hidden; */
}
.rating {
  position: absolute;
  z-index: 20;
  left: 20px;
  top: 3px;
  font-family: 'Acme', sans-serif;
}
.ticket {
  flex: 0.5;
}
.title {
  color: white;
  font-family: 'EB Garamond', serif;
  text-decoration: underline;
  text-shadow: 2px 2px 2px black;
  max-height: 60px;
  font-size: 5vw;
  font-weight: 100;
  flex: 2;
  overflow: scroll;
  white-space: normal;
  overflow-wrap: break-word;
}
.button-col {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 70px;
  flex: 0.5;
  overflow: visible;
}
.btn {
  margin: 0px;
  margin-right: 4px;
  border: none;
  border-radius: 2px;
  box-shadow: 0px 1px 0px 1px black;
  cursor: pointer;
  border-right: solid 1px black;
}
.del-tag {
  position: absolute;
  background-color: rgb(71, 71, 71);
  color: white;
  padding: 6px;
  border-radius: 4px;
  top: 26px;
  right: 4px;
  font-size: 10px;
  display: none;
  font-family: Arial, Helvetica, sans-serif;
}
.edit-tag {
  position: absolute;
  background-color: rgb(71, 71, 71);
  color: white;
  padding: 6px;
  border-radius: 4px;
  top: 60px;
  right: 10px;
  z-index: 101;
  font-size: 10px;
  display: none;
  font-family: Arial, Helvetica, sans-serif;
}
.delete-img,
.edit-img {
  width: 20px;
}
.delete {
  background-color: red;
}
.edit {
  background-color: rgb(182, 247, 122);
}
.hide {
  display: none;
}
.delete:hover + .del-tag {
  display: block;
}
.edit:hover + .edit-tag {
  display: block;
}
.rating-input {
  position: absolute;
  z-index: 21;
  width: 48px;
  left: 11px;
  font-size: 14px;
  top: 20px;
  border: solid 1px black;
}
